.wrapper .header {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100%;
  min-width: 1300px;
  height: 60px;
  background: #00ADAF;
}

.wrapper .ad {
  position: fixed;
  background: white;
  top: 130px;
  right: 20px;
  width: 200px;
  height: 200px;
  font-size: 12px;
  font-weight: 800;
  color: #333;
  padding: 10px;
  z-index: 1;
}

.wrapper .ad img {
  width: 180px;
  height: 100px;
}

.wrapper .ad .adText {
  font-size: 10px;
  color: #333;
  font-weight: 500;
  opacity: 0.6;
}

.wrapper .content {
  position: absolute;
  color: black;
  top: 70px;
  left: calc(50% - 500px);
  right: calc(50% - 500px);
  width: 1000px;
}